<template>
  <div class="content-container" direction="vertical">
    <el-container class="content-row">
      <el-button type="primary" @click="addCategory"></el-button>
    </el-container>
    <div>
      <el-table :data="categoryList" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="ID" width="100" prop="id">
        </el-table-column>
        <el-table-column label="" width="100" prop="name">
        </el-table-column>
        <el-table-column label="" width="500" prop="manager">
        </el-table-column>
        <el-table-column label="" width="200" prop="time">
          <template #default="scope">
            <el-button size="small" @click="deleteCategory(scope.$index)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      categoryList: [
        {
          id: 1231,
          name: "",
          manager: "01",
        },
        {
          id: 1131,
          name: "",
          manager: "01",
        },
        {
          id: 1031,
          name: "",
          manager: "01",
        },
      ],
    };
  },
  methods: {
    deleteCategory(index) {
      this.categoryList.splice(index, 1);
    },
    addCategory() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
      }).then(({ value }) => {
        this.categoryList.push({
          id: 1000,
          name: value,
          manager: "01",
        });
      }).catch((err) => {
        console.log(err);
      });
    },
  },
};
</script>
